.type--smallCap {
  font-family: 'roboto-bold';
  font-size: 0.75rem;
  text-transform: uppercase; }

h1 {
  font-family: 'signika-bold';
  font-size: 1.7em;
  margin: 7rem 0 3.5rem;
  max-width: 40rem; }

h2 {
  font-family: 'signika-bold';
  font-size: 1.5em; }

body {
  background: skyblue;
  color: skyblue;
  font-size: 0.875rem; }
  @media (min-width: 30rem) {
    body {
      font-size: 1rem;
      padding: 1rem; } }
  @media (min-width: 48rem) {
    body {
      font-size: 1.125rem; } }
  @media (min-width: 64rem) {
    body {
      font-size: 1.25rem; } }

.structure--background {
  background: white;
  min-height: calc(100vh - 2rem);
  padding: 6rem 1rem 1rem; }

.structure--column {
  margin: auto;
  max-width: 58rem;
  position: relative; }

.nav {
  position: absolute;
  right: 0;
  text-align: right;
  top: 0;
  width: 100%; }
  .nav__list {
    list-style: none;
    margin: 0;
    padding: 0; }
  .nav__item {
    display: inline-block; }
    .nav__item:nth-of-type(1n+2) {
      padding-left: 2rem; }



